/* global FB */
import React, { useEffect, useState } from "react";
import { Box, Button, Menu, Text, Footer, Grommet } from "grommet";
import "../pages/index.css";
import "./i18n";
import { assign } from "../helpers";
import { Tools } from "grommet-icons";
import { navigate } from "gatsby-link";
import Parse from "../library/parse";
import { connect } from "react-redux";
import { storeLoginDetails } from "../state/actions";

const logIn = async () => {
  return Parse.FacebookUtils.logIn("public_profile,email", {}).then((user) => {
    if (!user.existed()) {
      FB.api("/me?fields=id,name,email,permissions", function (response) {
        return Parse.Cloud.run("saveUserProfile", {
          email: response.email,
          fullName: response.name,
        });
      });
    }
  });
};

const theme = {
  global: {
    font: {
      family: "Roboto",
      size: "18px",
      height: "20px",
    },
  },
};

const Home = ({ children, login }) => {
  const [accountMenu, setAccountMenu] = useState([]);
  const [username, setUsername] = useState("Guest");
  const [allowStore, setAllowStore] = useState(false)
  useEffect(() => {
    const user = Parse.User.current();
    user ? setUsername(Parse.User.current().get("email"))
    : setUsername("Guest");
    if(user && user.get('allowStore')) {
      setAllowStore(true)
    }
  }, [children]);

  useEffect(() => {
    const logoutMenu = [
      {
        label: "Messenger",
        onClick: () => {
          navigate("/chat");
        },
      },
      {
        label: "My Orders",
        onClick: () => {
          navigate("/order");
        },
      },
      {
        label: "Sign Out",
        onClick: () => {
          Parse.User.logOut().then(() => {
            setUsername("Guest");
            navigate("/");
          });
        },
      },
    ];

    allowStore && logoutMenu.push({
      label: "My Store",
      onClick: () => {
        navigate("/account/store");
      },
    })
    const accMenu =
      username === "Guest"
        ? [
            {
              label: "Sign in Using Facebook",
              onClick: async () => {
                await logIn();
                login(Parse.User.current());
                setUsername(Parse.User.current().get("email"));
                navigate("/");
              },
            },
          ]
        : logoutMenu;
    setAccountMenu(accMenu);
  }, [username]);

  return (
    <Grommet theme={theme}>
      <Box>
        <Box
          gridArea="header"
          direction="row"
          align="center"
          pad={{ horizontal: "medium", vertical: "small" }}
          background="brand"
        >
          <Box>
            <Button
              icon={<Tools />}
              label="BidBid"
              margin={{ right: "20px" }}
              onClick={() => {
                assign("/");
              }}
            />
          </Box>
          <Box margin={{ left: "auto" }}>
            <Menu label={username} items={accountMenu} />
          </Box>
        </Box>

        <Box align="center" justify="center" pad="small">
          <Box
            pad={{
              bottom: "64px",
            }}
            fill="horizontal"
            width={{
              min: "360px",
            }}
          >
            {children}
          </Box>
        </Box>
        <Footer margin={{ top: "auto" }} background="brand" pad="medium">
          <Text>Copyright</Text>
        </Footer>
      </Box>
    </Grommet>
  );
};

const mapStateToProps = (state) => {
  return { isLogin: state.todos };
};

const mapDispatchToProps = (dispatch) => {
  return {
    login: (details) => {
      dispatch(storeLoginDetails(details));
    },
  };
};

export default connect(mapStateToProps, mapDispatchToProps)(Home);
